body {
  box-sizing: border-box;
  background-image: url("bg_cat.73f6b984.jpg");
  position: relative;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

img {
  width: 40%;
  height: 40%;
  display: block;
}

.cat-info {
  border-radius: 10px;
  outline: 1px solid #9bc9b8;
  justify-content: center;
  align-items: center;
  gap: 50px;
  margin-top: 10px;
  display: flex;
}

.cat-info p {
  width: 600px;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  display: flex;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 500px;
  height: 250px;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-text {
  box-sizing: border-box;
  text-align: center;
  flex-wrap: nowrap;
  justify-content: center;
  margin-top: auto;
  margin-bottom: auto;
  font-family: Roboto, sans-serif;
  display: flex;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.close-btn {
  width: 143px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #000;
  border-radius: 32px;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.close-btn:-webkit-any(:hover, :focus) {
  background-color: rgba(246, 246, 246, .3);
}

.close-btn:is(:hover, :focus) {
  background-color: rgba(246, 246, 246, .3);
}

iframe {
  position: fixed;
  bottom: 0;
  left: 0;
}

